.contentContainer {
  height: 80vh;
  overflow-y: scroll;
}
.stickyContainer {
  position: -webkit-sticky;
  position: sticky;
  margin: 0;
  padding-bottom: 6px;
  opacity: 1;
  background-color: white;
  top: 0;
}
#background {
  width: 100vw;
  height: 100%;
  position: fixed;
  left: 0;
  top: 30px;
  opacity: 0.8;
}
.text {
  position: absolute;
  z-index: 999;
  margin: 0 auto;
  left: 0;
  right: 0;
  text-align: center;
  top: 20vw;
}
.text h1 {
  font-size: 150%;
  font-family: Impact, sans-serif;
}
#languageSelection {
  width: 150px;
  margin: auto;
}


#chapterContainer {
  border: 0.5px solid;
  border-color: LightGray;
  border-radius: 10px;
  padding: 15px;
  width: 100%;
  height: 77vh;
  overflow-y: scroll;
  white-space: pre-line; /* To keep original formatting of text */
}
#vocabularyContainer {
  border: 0.5px solid;
  border-color: LightGray;
  border-radius: 10px;
  width: 100%;
  height: 77vh;
  overflow-y: scroll;
}
#stickyWithPadding {
  padding-top: 15px;
  padding-left: 15px;
  padding-right: 15px;
}
#vocabulary {
  padding-left: 15px;
  padding-right: 15px;
}
#chapterContainer h1 {
  font-size: x-large;
}
#vocabularyContainer h2 {
  font-size: x-large;
}

.open .dropdown-menu {
  display: block;
  top: auto;
}
  
#aboutPage h1 {
  font-size: xx-large;
}

#aboutPage ul {
  display: block;
  list-style-type: none;
  margin: 0;
  padding: 0;
}
